import React from 'react'
import ProblemActivity from '../../../generics/activity/problem_activity'

/*ESTE COMPONENTE DEVE RECEBER COMO PROPRIEDADE O SEGUINTE ITEM:
    unitId: String, representa o id da unidade em que a atividade se encontra
.*/
class Problemas extends React.Component{
    constructor(props) {
        super(props)

        this.validateProps()
    }

    validateProps = () => {
        if (this.props.unitId === undefined) {
            throw Error("Property 'unitId' can't be undefined!")
        }
    }

    getProblems = () => {
        return {
            unitId: this.props.unitId,
            title: "Elabore um algoritmo para cada um dos problemas a seguir descritos.",
            problems: [
                {
                    id: "problema_um_algoritmos",
                    title: "Problema 1",
                    text: "Elaborao de uma histria, produzida de forma  cooperativa por um grupo de no mximo 5 alunos: considere que a histria ser escrita por todos os 5 alunos, mas que cada um dever escrever pargrafos individualmente (sem conversar com os seus pares), tomando por base apenas o texto que j est escrito e dando continuidade ao mesmo. Os participantes devem estar separados, sem contato direto, e o texto dever circular em uma determinada ordem. Cada um ter sua vez para escrever e, para tanto, ter em mos o que foi escrito at o momento, e com base nesse material dever produzir um novo pargrafo de at 5 linhas. O ttulo, o gnero e o primeiro pargrafo da histria devem ser produzidos a partir de uma conversa inicial entre os participantes. A escrita dever ser completada em at 5 rodadas.",
                    maxAnswerLength: 1000
                },
                {
                    id: "problema_dois_algoritmos",
                    title: "Problema 2",
                    text: "Descreva a construo de um crculo, dividido em 8 partes iguais, usando um compasso, uma rgua e um lpis.",
                    maxAnswerLength: 1000
                },
                {
                    id: "problema_tres_algoritmos",
                    title: "Problema 3",
                    text: "Descreva o algoritmo para determinao do mximo divisor comum entre dois nmeros.",
                    maxAnswerLength: 1000
                },
                {
                    id: "problema_quatro_algoritmos",
                    title: "Problema 4",
                    text: "Descreva a preparao de uma sopa de legumes. Considere seus ingredientes prediletos.",
                    maxAnswerLength: 1000
                }
            ]
        }
    }

    render() {
        return (
            <ProblemActivity activity={this.getProblems()}></ProblemActivity>
        )
    }
}

export default Problemas