import React from 'react'
import IntegralActivity from '../../../generics/activity/integral_activity'

/*ESTE COMPONENTE DEVE RECEBER COMO PROPRIEDADE O SEGUINTE ITEM:
    unitId: String, representa o id da unidade em que a atividade se encontra
.*/
class AtividadeAvaliativaUm extends React.Component {
    constructor(props) {
        super(props)

        this.validateProps()
    }

    validateProps = () => {
        if(this.props.unitId === undefined){
            throw Error("Property 'unitId' can't be undefined!")
        }
    }

    getActivity = () => {
        return {
            unitId: this.props.unitId,
            id: "atividade_1_abstracao",
            questions: [
                {
                    id: "q1_a1_abstracao",
                    title: "",
                    statement: "A1. Para falar das atividades necessrias para detalhar a abstrao Organizar o seu guarda-roupa, identifique a atividade que lhe parece menos adequada:",
                    options: [
                        {
                            value: "0",
                            key: "1",
                            text: "Separar as peas de roupa por tipo.",
                            tip: "Essa atividade  adequada, pois garante assertividade na hora de escolher um tipo determinado de roupa."
                        },
                        {
                            value: "0",
                            key: "2",
                            text: "Para cada tipo de pea, organizar por cores.",
                            tip: "Essa atividade  interessante, pois garante que alm do tipo de pea voc tenha a diviso por cores."
                        },
                        {
                            value: "0",
                            key: "3",
                            text: "Guardar cada pea no espao reservado para o seu tipo.",
                            tip: "Essa atividade  totalmente adequada, garantindo que voc saiba a posio exata de cada tipo."
                        },
                        {
                            value: "1",
                            key: "4",
                            text: "Separar as peas por cor, independentemente do tipo.",
                            tip: "Separar as peas por cores provavelmente vai ajudar a deixar seu guarda-roupas com um \"layout\" bonito, mas no vai ser nada prtico na hora que voc for se vestir."
                        }
                    ]
                }
            ]
        }
    }

    render() {
        return (
            <IntegralActivity activity={this.getActivity()} />
        )
    }
}

export default AtividadeAvaliativaUm