import React from 'react'
import IntegralActivity from '../../../generics/activity/integral_activity'

/*ESTE COMPONENTE DEVE RECEBER COMO PROPRIEDADE O SEGUINTE ITEM:
    unitId: String, representa o id da unidade em que a atividade se encontra
.*/
class AtividadeAvaliativaTres extends React.Component {
    constructor(props) {
        super(props)

        this.validateProps()
    }

    validateProps = () => {
        if(this.props.unitId === undefined){
            throw Error("Property 'unitId' can't be undefined!")
        }
    }

    getActivity = () => {
        return {
            unitId: this.props.unitId,
            id: "atividade_3_abstracao",
            questions: [
                {
                    id: "q1_a3_abstracao",
                    title: "",
                    statement: "A4. Para organizar o seu guarda-roupa, o que lhe parece ser uma abstrao mais conveniente para ajud-lo na hora de escolher o que vestir: ",
                    options: [
                        {
                            value: "0",
                            key: "1",
                            text: "O tecido com qual as peas so confeccionadas.",
                            tip: "O tecido de fabricao das peas pode ajudar a conhecer mais sobre tipos de tecido, mas no parece uma boa abstrao para organizar um guarda-roupa, que  o objetivo proposto."
                        },
                        {
                            value: "0",
                            key: "2",
                            text: "O pas onde as peas so fabricadas.",
                            tip: "Saber o pas onde as peas so fabricadas ajudaria voc em Geografia, mas dificilmente vai gerar uma boa abstrao."
                        },
                        {
                            value: "0",
                            key: "3",
                            text: "O preo das peas.",
                            tip: "O preo de uma pea de roupa muitas vezes no consegue relacionar-se diretamente com sua qualidade, assim tambm no  uma boa abstrao."
                        },
                        {
                            value: "1",
                            key: "4",
                            text: "O tipo das peas, por exemplo, cala, bermuda, vestido, saia, blazer, etc.",
                            tip: "Saber exatamente qual o tipo de pea est em qual posio do guarda-roupa vai lhe ajudar a planejar seu \"look\" e  a melhor abstrao possvel neste caso."
                        },
                    ]
                },
                {
                    id: "q2_a3_abstracao",
                    title: "",
                    statement: "A5. Para organizar a circulao de veculos de uma cidade, o que parece ser uma caracterstica irrelevante de suas vias:",
                    options: [
                        {
                            value: "0",
                            key: "1",
                            text: "A largura da via.",
                            tip: "A largura da via pode impedir que mais de um veculo circule pela mesma simultaneamente, sendo uma caracterstica muito relevante."
                        },
                        {
                            value: "0",
                            key: "2",
                            text: "A altura mxima permitida para o trfego de veculos.",
                            tip: "A altura mxima garante por exemplo que um caminho no v ficar preso em uma passarela ou viaduto, sendo uma caracterstica muito relevante."
                        },
                        {
                            value: "1",
                            key: "3",
                            text: "A idade mdia dos veculos que trafegam na via.",
                            tip: "A idade mdia de um veculo (a no ser que esse seja muito velho) no vai ser um item que evite sua circulao na via."
                        },
                        {
                            value: "0",
                            key: "4",
                            text: "A tonelagem mxima suportada pela via.",
                            tip: "A tonelagem mxima suportada pela via define se pela mesma podem circular veculos de carga ou de grande capacidade de passageiros, sendo uma caracterstica muito relevante."
                        }
                    ]
                }
            ]
        }
    }

    render() {
        return (
            <IntegralActivity activity={this.getActivity()} />
        )
    }
}

export default AtividadeAvaliativaTres