import React from 'react'
import IntegralActivity from '../../../generics/activity/integral_activity'

/*ESTE COMPONENTE DEVE RECEBER COMO PROPRIEDADE O SEGUINTE ITEM:
    unitId: String, representa o id da unidade em que a atividade se encontra
.*/
class AtividadeAvaliativaUm extends React.Component {
    constructor(props) {
        super(props)

        this.validateProps()
    }

    validateProps = () => {
        if (this.props.unitId === undefined) {
            throw Error("Property 'unitId' can't be undefined!")
        }
    }

    getActivity = () => {
        return {
            unitId: this.props.unitId,
            id: "atividade_1_algoritmos",
            statement: "A1. Considere o Algoritmo 02, a seguir, sobre o qual fazemos algumas afirmaes. Identifique a alternativa que parece menos apropriada para se referir a este algoritmo.",
            questions: [
                {
                    id: "q1_a1_algoritmos",
                    title: "Algoritmo 02",
                    statement: "",
                    algorithm: true,
                    preInstructions: "<span class=\"textdarkgreen\">Considere que: </br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;a)Dois recipientes denominados R5 e R3 tm capacidade para 5 e 3 litros respectivamente;</br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;b)Os dois recipientes esto inicialmente vazios.</span>",
                    instructions: [
                        {
                            text: "Encha de gua o recipiente R5;"
                        },
                        {
                            text: "Despeje o contedo de R5 no recipiente R3;"
                        },
                        {
                            text: "Esvazie R3;"
                        },
                        {
                            text: "Despeje o contedo de R5 em R3;"
                        },
                        {
                            text: "Encha de gua o R5;"
                        },
                        {
                            text: "Respeje o contedo de R5 necessrio para completar a capacidade de R3;"
                        },
                        {
                            text: "Esvazie R3."
                        },
                    ],
                    options: [
                        {
                            value: "0",
                            key: "1",
                            text: "O algoritmo descreve uma sequncia de operaes cuja execuo completa nos permite obter exatos 4 litros de gua no recipiente R5.",
                            tip: "De fato, seguindo essa sequncia sero obtidos 4 litros de gua no recipiente R5."
                        },
                        {
                            value: "0",
                            key: "2",
                            text: "Se retirarmos a Operao 7, o algoritmo passa a descrever uma soluo para se obter 7 litros de gua.",
                            tip: "De fato, no recipiente R5 teremos 4 litros e no recipiente R3 teremos 3 litros, considerando a adio do contedo de R5 com o contedo de R3, obteremos 7 litros. Executando a Operao 4, obteremos 2 litros em R3 e 0 litros em R5 e, finalmente executando a Operao 5, teremos 5 litros em R5 e 2 litros em R3. A soma dos contedos dos 2 recipientes totalizar 5."
                        },
                        {
                            value: "0",
                            key: "3",
                            text: "Para obter 7 litros de gua podemos retirar as duas ltimas operaes do Algoritmo 02.",
                            tip: "De fato, executando a Operao 1 obteremos 5 litros de gua em R5 e o recipiente R3 continuar vazio. Executando a Operao 2, obteremos 2 litros em R5 e 3 litros em R3. Executando a Operao 3, obteremos 2 litros em R5 e 0 litros em R3."
                        },
                        {
                            value: "1",
                            key: "4",
                            text: "A execuo do algoritmo resultar em 4 litros de gua no recipiente R3.",
                            tip: " impossvel obter 4 litros em um recipiente que s comporta 3 litros."
                        }
                    ]
                }
            ]
        }
    }

    render() {
        return (
            <IntegralActivity activity={this.getActivity()} />
        )
    }
}

export default AtividadeAvaliativaUm