import React from 'react'
import IntegralActivity from '../../../generics/activity/integral_activity'

/*ESTE COMPONENTE DEVE RECEBER COMO PROPRIEDADE O SEGUINTE ITEM:
    unitId: String, representa o id da unidade em que a atividade se encontra
.*/
class AtividadeAvaliativaUm extends React.Component {
    constructor(props) {
        super(props)

        this.validateProps()
    }

    validateProps = () => {
        if (this.props.unitId === undefined) {
            throw Error("Property 'unitId' can't be undefined!")
        }
    }

    getActivity = () => {
        return {
            unitId: this.props.unitId,
            id: "atividade_1_decomposicao",
            questions: [
                {
                    id: "q1_a1_decomposicao",
                    title: "Qual das afirmativas abaixo lhe parece menos apropriada para se falar sobre o Pilar Decomposio?",
                    statement: "",
                    options: [
                        {
                            value: "1",
                            key: "1",
                            text: " uma atividade totalmente subjetiva, pois uma boa decomposio independe do objeto ou sistema que desejamos decompor e das finalidades s quais ela servir.",
                            tip: "Diferentes pessoas provavelmente definiro diferentes decomposies. Entretanto, independentemente de como a decomposio seja realizada, a natureza do objeto ou sistema a ser decomposto obrigatoriamente deve ser considerada."
                        },
                        {
                            value: "0",
                            key: "2",
                            text: "O resultado de uma decomposio so partes ou sistemas mais simples os quais, entretanto, podem serem adequados a novas  decomposies.",
                            tip: "De fato, um ou mais elementos de uma decomposio podem ainda serem complexos e com isso requererem novas decomposies."
                        },
                        {
                            value: "0",
                            key: "3",
                            text: "Quando um mesmo sistema  decomposto isoladamente por duas ou mais pessoas,   possvel que tenhamos decomposies diferentes.",
                            tip: "Certamente, o nosso olhar sobre o objeto que vamos decompor alm da nossa compreenso sobre os objetivos da situao-problema podem contribuir para diferentes decomposies e ainda assim ambas serem apropriadas aos propsito da situao-problema."
                        },
                        {
                            value: "0",
                            key: "4",
                            text: " possvel comparar duas decomposies de um mesmo sistema ou objeto e com isso identificar entre elas a que parece mais adequada aos propsitos do problema a ser resolvido.",
                            tip: "De fato, duas ou mais pessoas, ao decompor um sistema ou objeto, apesar de levarem em conta a situao-problema, sem dvida podem produzir decomposies diferente. Uma avaliao e comparao das solues pode resultar que uma delas se mostre superior ou at que uma nova decomposio ainda melhor do que as incialmente produzidas possa ser identificada."
                        }
                    ]
                }
            ]
        }
    }

    render() {
        return (
            <IntegralActivity activity={this.getActivity()} />
        )
    }
}

export default AtividadeAvaliativaUm
