import React from 'react'
import IntegralActivity from '../../../generics/activity/integral_activity'

/*ESTE COMPONENTE DEVE RECEBER COMO PROPRIEDADE O SEGUINTE ITEM:
    unitId: String, representa o id da unidade em que a atividade se encontra
.*/
class AtividadeAvaliativaDois extends React.Component {
    constructor(props) {
        super(props)

        this.validateProps()
    }

    validateProps = () => {
        if (this.props.unitId === undefined) {
            throw Error("Property 'unitId' can't be undefined!")
        }
    }

    getActivity = () => {
        return {
            unitId: this.props.unitId,
            id: "atividade_2_decomposicao",
            questions: [
                {
                    id: "q1_a2_decomposicao",
                    title: "A1. Organizando um evento escolar",
                    statement: "Qualquer evento, seja no mbito pessoal (aniversrios, casamentos, festas religiosas, etc.) quanto no profissional (congressos, seminrios, premiaes, etc.), requer, para sua realizao, a execuo de um conjunto de atividades que podem, dependendo da dimenso do evento em questo, envolver vrias pessoas ou equipes, responsveis pelos diferentes aspectos que constituem o evento. Considere a realizao do seminrio \"O Pensamento Computacional na Escola\", e dentre as diferentes listas apresentadas nas alternativas , indique qual lhe parece uma decomposio adequada de elementos envolvidos nesse evento.",
                    options: [
                        {
                            value: "0",
                            key: "1",
                            text: "Convidados; local; refrigerao; mdia/transmisso; comisso julgadora.",
                            tip: "Essa decomposio est mais adequada para um desfile de modas do que para um seminrio."
                        },
                        {
                            value: "0",
                            key: "2",
                            text: "Divulgao; patrocnio; local; atraes; licenciamento; ingressos; segurana.",
                            tip: "Essa decomposio est mais adequada para um show de msica do que para um seminrio."
                        },
                        {
                            value: "0",
                            key: "3",
                            text: "Local; convites; figurino; sonorizao; apresentao.",
                            tip: "Essa decomposio est mais adequada para uma apresentao em um teatro do que para um seminrio."
                        },
                        {
                            value: "1",
                            key: "4",
                            text: "Inscries; data; patrocnio; comits; local; transporte; programao.",
                            tip: "Essa  uma decomposio adequada para um seminrio."
                        },
                        {
                            value: "0",
                            key: "5",
                            text: "Convites; cardpio; msica; bolo decorado; doces; salgados; bebidas; lembranas.",
                            tip: "Essa decomposio est mais adequada para uma festa de aniversrio do que para um seminrio."
                        },
                    ]
                },
                {
                    id: "q2_a2_decomposicao",
                    title: "A2. Diagnose mdica",
                    statement: "Com o propsito de diagnosticar a origem de um problema de sade, a partir de sintomas diversos, um mdico usualmente analisa o corpo humano segundo seus sistemas ou elementos constituintes. Em tal cenrio, qual dos seguintes conjuntos lhe parece menos adequado.",
                    options: [
                        {
                            value: "0",
                            key: "1",
                            text: "Cardiovascular, respiratrio, digestivo, nervoso, sensorial, endcrino, excretor, urinrio, reprodutor, esqueltico, muscular, imunolgico, linftico, tegumentar.",
                            tip: "Essa decomposio possui uma quantidade de elementos que pode ajudar em um diagnstico de um clnico geral."
                        },
                        {
                            value: "0",
                            key: "2",
                            text: "Cavidades nasais, faringe, laringe, traquia, brnquios, pulmes.",
                            tip: "Essa decomposio possui uma quantidade de elementos que pode ajudar em um diagnstico do sistema respiratrio."
                        },
                        {
                            value: "1",
                            key: "3",
                            text: "Cabea, tronco, membros.",
                            tip: "Essa decomposio possui poucos elementos e dificultaria um diagnstico."
                        },
                        {
                            value: "0",
                            key: "4",
                            text: "Viso, audio, olfato, paladar, tato.",
                            tip: "Essa decomposio possui uma quantidade de elementos que pode ajudar em um diagnstico de um otorrinolaringologista."
                        },
                        {
                            value: "0",
                            key: "5",
                            text: "Artrias, veias, vasos capilares.",
                            tip: "Essa decomposio possui uma quantidade de elementos que pode ajudar em um diagnstico do sistema circulatrio."
                        },
                    ]
                }
            ]
        }
    }

    render() {
        return (
            <IntegralActivity activity={this.getActivity()} />
        )
    }
}

export default AtividadeAvaliativaDois
