import React from 'react'
import IntegralActivity from '../../../generics/activity/integral_activity'

/*ESTE COMPONENTE DEVE RECEBER COMO PROPRIEDADE O SEGUINTE ITEM:
    unitId: String, representa o id da unidade em que a atividade se encontra
.*/
class AtividadeAvaliativaUm extends React.Component {
    constructor(props) {
        super(props)

        this.validateProps()
    }

    validateProps = () => {
        if (this.props.unitId === undefined) {
            throw Error("Property 'unitId' can't be undefined!")
        }
    }

    getActivity = () => {
        return {
            unitId: this.props.unitId,
            id: "atividade_1_reconhecimento_de_padroes",
            questions: [
                {
                    id: "q1_a1_reconhecimento_de_padroes",
                    title: "",
                    statement: "A1. Passeando pelo centro de uma cidade conhecida, digamos, o Rio de Janeiro, voc se depara com um aglomerado de pessoas. De repente um desconhecido estabelece uma conversa com voc, buscando tambm entender o que est acontecendo. Essa conversa segue, levantando hipteses e trocando opinies, quando percebem que j se passaram uns 30 minutos e se despedem sem descobrirem o motivo da aglomerao. Pensando na conversa com o estranho, voc se d conta que o mesmo no parecia ser carioca nato ou criado no Rio. Motivado pela curiosidade, voc tenta reconhecer em qual estado aquela pessoa teria nascido e/ou sido criada. Qual, dentre os elementos a seguir, seria o menos provvel de estabelecer um padro adequado a tal propsito?",
                    options: [
                        {
                            value: "0",
                            key: "1",
                            text: "Sotaque.",
                            tip: "Seria possvel diferenciar o sotaque, por exemplo, de um gacho ou de um carioca."
                        },
                        {
                            value: "0",
                            key: "2",
                            text: "Uso de expresses idiomticas.",
                            tip: "Com certeza um \"bah\" ou um \"tch\" revelaria um gacho, oxente um nordestino."
                        },
                        {
                            value: "1",
                            key: "3",
                            text: "Uso de roupas formais.",
                            tip: "Embora o uso de trajes tpicos de uma regio possa identificar a origem de uma pessoa, o uso de roupas formais, como por exemplo terno e gravata para homens,  universal."
                        },
                        {
                            value: "0",
                            key: "4",
                            text: "Referncias a elementos regionais.",
                            tip: "Falar de um prato tpico ou de um monumento pode indicar a origem de uma pessoa."
                        },
                        {
                            value: "0",
                            key: "5",
                            text: "Preferncia por estilo musical.",
                            tip: "Certos estilos musicais so bem caractersticos de certos estados e regies do Pas."
                        }
                    ]
                }
            ]
        }
    }

    render() {
        return (
            <IntegralActivity activity={this.getActivity()} />
        )
    }
}

export default AtividadeAvaliativaUm