import React from 'react'
import Title from '../../../../generics/title'
import CenterBoxContainer from '../../../../generics/center_box_container'
import AlgorithmLevel from '../../../../generics/algorithm_level'
import AlgorithmBox from '../../../../generics/algorithm_box'

const AplicacoesParte3 = () => (
    <div>
        <h3>Uma experincia escolar em tempo de Cultura Digital</h3>
        <p>Professores diariamente precisam propor atividades para seus alunos, buscando apoi&aacute;-los na constru&ccedil;&atilde;o de conhecimento sobre determinados assuntos. No caso a seguir, estamos supondo que os alunos tenham acesso a computadores e Internet, seja no laborat&oacute;rio de Inform&aacute;tica da escola ou mesmo em suas resid&ecirc;ncias. Assim, estamos considerando a possibilidade do uso de editor de texto, consultas &agrave; Internet e troca de mensagens por email.</p>
        <CenterBoxContainer>
            <AlgorithmBox>
                <Title>Algoritmo 10  Explorando um tema na sala de aulao</Title><br />
                <p className="textblue pcenter">(uma experincia de aprendizagem cooperativa na cultura digital)</p>
                <AlgorithmLevel>
                    <span className="textdarkgreen"><strong>Pr condies</strong>: sentados em suas mesas, dispostas em crculo, os alunos recebem um texto selecionado pelo(a) professor(a).</span><br />
                    <AlgorithmLevel showStyle={true} child={true}>
                        <li>Ler o texto atentamente;</li>
                        <li>Elaborar e registrar, em uma folha com seu nome, 2 questes sobre o texto lido;</li>
                        <li>Passar as suas questes para o colega que est  sua esquerda;</li>
                        <li>Receber a lista de questes que lhe foi passada pelo colega que est  sua direita;</li>
                        <li>Responder uma das questes;</li>
                        <li>Passar a lista para o colega que est  sua esquerda;</li>
                        <li>Receber uma nova lista que lhe foi passada pelo colega que est  sua direita;</li>
                        <li>Responder uma das questes ainda no respondidas;</li>
                        <li>Entregar a lista que est em suas mos ao colega que a escreveu;</li>
                        <li>Em casa, digitar a sua questo e as respostas recebidas e compartilh-lhas por email com o professor;</li>
                        <li>Fazer pesquisas nos livros e na Internet e escrever um pequeno texto com as respostas s suas perguntas e enviar por email para todos os colegas e para o professor.</li>
                    </AlgorithmLevel>
                    <span className="textdarkgreen">Posteriormente o professor far uma compilao das perguntas e respostas e distribuir para todos os alunos. Em outro momento presencial, a turma discutir sobre as questes que surgiram e as diferentes respostas, para consolidar as aprendizagens.</span><br />
                </AlgorithmLevel>
            </AlgorithmBox>
        </CenterBoxContainer>
    </div>
)

export default AplicacoesParte3