import React from 'react'
import IntegralActivity from '../../../generics/activity/integral_activity'

/*ESTE COMPONENTE DEVE RECEBER COMO PROPRIEDADE O SEGUINTE ITEM:
    unitId: String, representa o id da unidade em que a atividade se encontra
.*/
class AtividadeAvaliativaDois extends React.Component {
    constructor(props) {
        super(props)

        this.validateProps()
    }

    validateProps = () => {
        if (this.props.unitId === undefined) {
            throw Error("Property 'unitId' can't be undefined!")
        }
    }

    getActivity = () => {
        return {
            unitId: this.props.unitId,
            id: "atividade_3_decomposicao",
            questions: [
                {
                    id: "q1_a3_decomposicao",
                    title: "A1. Avaliando um festival folclrico",
                    statement: "Considere que voc precisa definir como deve ser realizada a avaliao das danas apresentadas por grupos em um festival folclrico. Considere ainda que esse festival seja realizado pela comunidade de uma escola, de um bairro ou at de uma cidade inteira. Reflita sobre como a avaliao poderia ser decomposta em elementos e indique qual, dentre os seguintes, seria menos adequado para esse fim.",
                    options: [
                        {
                            value: "0",
                            key: "1",
                            text: "Tempo da apresentao.",
                            tip: "Com certeza seria possvel estabelecer um tempo mximo de apresentao e utilizar essa mtrica para avaliar a performance do grupo."
                        },
                        {
                            value: "0",
                            key: "2",
                            text: "Sincronia.",
                            tip: "Um grupo que treinou adequadamente deve estar em sintonia, com sincronia de movimentos, sendo essa uma boa alternativa de avaliao."
                        },
                        {
                            value: "0",
                            key: "3",
                            text: "Preciso de movimentos.",
                            tip: "Movimentos precisos esto associados com dedicao aos treinos e podem ser uma tima forma de avaliar os grupos."
                        },
                        {
                            value: "1",
                            key: "4",
                            text: "Participao da audincia.",
                            tip: "A participao da audincia no tem influncia direta na dana apresentada pelos grupos no sendo uma forma correta de avaliar os grupos."
                        },
                        {
                            value: "0",
                            key: "5",
                            text: "Figurino.",
                            tip: "O figurino est geralmente associado com o tema a ser apresentado pelos grupos e seria uma boa forma de avaliar os mesmos."
                        },
                    ]
                }
            ]
        }
    }

    render() {
        return (
            <IntegralActivity activity={this.getActivity()} />
        )
    }
}

export default AtividadeAvaliativaDois
