import React from 'react'
import IntegralActivity from '../../../generics/activity/integral_activity'

/*ESTE COMPONENTE DEVE RECEBER COMO PROPRIEDADE O SEGUINTE ITEM:
    unitId: String, representa o id da unidade em que a atividade se encontra
.*/
class AtividadeAvaliativaDois extends React.Component {
    constructor(props) {
        super(props)

        this.validateProps()
    }

    validateProps = () => {
        if(this.props.unitId === undefined){
            throw Error("Property 'unitId' can't be undefined!")
        }
    }

    getActivity = () => {
        return {
            unitId: this.props.unitId,
            id: "atividade_2_abstracao",
            questions: [
                {
                    id: "q1_a2_abstracao",
                    title: "",
                    statement: "A2. Considere a organizao de uma biblioteca e indique o que lhe parece ser uma abstrao mais conveniente para falar de livros:",
                    options: [
                        {
                            value: "0",
                            key: "1",
                            text: "Cores das capas.",
                            tip: "A cor da capa pode despertar nosso lado artstico, mas no vai trazer informaes sobre o contedo dos livros!"
                        },
                        {
                            value: "1",
                            key: "2",
                            text: "Autores dos livros.",
                            tip: "Se organizarmos os livros pelos seus autores, saberemos qual o estilo de escrita dos mesmos e se escrevem sobre fico, romances, textos tcnicos, etc, sendo uma boa opo de abstrao."
                        },
                        {
                            value: "0",
                            key: "3",
                            text: "Tamanho das letras.",
                            tip: "O tamanho das letras ajuda na leitura, mas no vai trazer informaes sobre o contedo dos livros!"
                        },
                        {
                            value: "0",
                            key: "4",
                            text: "Gramatura do papel.",
                            tip: "A gramatura do papel, pode revelar traos da qualidade dos livros, mas no vai trazer informaes sobre o contedo dos mesmos!"
                        }
                    ]
                },
                {
                    id: "q2_a2_abstracao",
                    title: "",
                    statement: "A3. Para organizar os produtos da rea de vendas ao consumidor, o que lhe parece ser um conjunto menos adequado de setores:",
                    options: [
                        {
                            value: "0",
                            key: "1",
                            text: "Padaria, Materiais de Limpeza, Congelados, Hortigranjeiros.",
                            tip: "Esses so setores adequados, correspondendo ao que geralmente encontramos em um mercado."
                        },
                        {
                            value: "1",
                            key: "2",
                            text: "Produtos da empresa X, Produtos da Empresa Y, Produtos da Empresa Z.",
                            tip: "Este conjunto no  adequado, pois as empresas podem ter segmentos de produtos diferentes, no contribuindo para a organizao e facilidade de escolha dos clientes."
                        },
                        {
                            value: "0",
                            key: "3",
                            text: "Higiene pessoal, Laticinios, Adega, Bomboniere, Aougue.",
                            tip: "Esses so setores adequados, correspondendo ao que geralmente encontramos em um mercado."
                        },
                        {
                            value: "0",
                            key: "4",
                            text: "Frios, Congelados, Matinais, Materiais Eltricos.",
                            tip: "Esses so setores adequados, correspondendo ao que geralmente encontramos em um mercado."
                        }
                    ]
                }
            ]
        }
    }

    render() {
        return (
            <IntegralActivity activity={this.getActivity()} />
        )
    }
}

export default AtividadeAvaliativaDois