import React from 'react'
import IntegralActivity from '../../../generics/activity/integral_activity'

/*ESTE COMPONENTE DEVE RECEBER COMO PROPRIEDADE O SEGUINTE ITEM:
    unitId: String, representa o id da unidade em que a atividade se encontra
.*/
class AtividadeAvaliativaQuatro extends React.Component {
    constructor(props) {
        super(props)

        this.validateProps()
    }

    validateProps = () => {
        if (this.props.unitId === undefined) {
            throw Error("Property 'unitId' can't be undefined!")
        }
    }

    getActivity = () => {
        return {
            unitId: this.props.unitId,
            id: "atividade_4_algoritmos",
            statement: "A04. O Algoritmo 14 abaixo descreve o processo de retirada de um dos 4 pneus de um automvel. Fazemos a seguir algumas afirmaes sobre ele. Selecione a afirmao que voc considera menos apropriada para se referir a este algoritmo.",
            questions: [
                {
                    id: "q1_a4_algoritmos",
                    title: "Algoritmo 14",
                    statement: "",
                    algorithm: true,
                    instructions: [
                        {
                            text: "Pegue o macaco;"
                        },
                        {
                            text: "Pegue um recipiente vazio onde sero colocados os parafusos da roda;"
                        },
                        {
                            text: "Posicione o macaco no ponto indicado para levantar o carro com respeito a uma dada roda;"
                        },
                        {
                            text: "Acione um pouco o macaco, sem tirar o carro do piso;"
                        },
                        {
                            text: "Afrouxe todos os parafusos da roda;"
                        },
                        {
                            text: "Levante o carro, deixando-o cerca de 10 centmetros acima do piso;"
                        },
                        {
                            text: "<span class=\"textred\">Enquanto</span> <span class=\"textblue\">houver parafuso por retirar da roda</span> <span class=\"textred\">faa</span>:",
                            instructions: [
                                {
                                    text: "a. Retire um dos parafusos restantes na roda;"
                                },
                                {
                                    text: "b. Coloque o parafuso retirado da roda dentro de um recipiente;"
                                },
                                {
                                    text: "c. Coloque na roda um dos parafusos disponveis no recipiente."
                                }
                            ]
                        },
                        {
                            text: "Tire o pneu."
                        }
                    ],
                    options: [
                        {
                            value: "0",
                            key: "1",
                            text: "A Operao 7 descreve uma repetio que nunca termina, pois a Operao 7.c tem efeito contrrio ao que  realizado na Operao 7.a.",
                            tip: "De fato pois  na Operao 7.a um dos parafusos  retirado e em 7.c um parafuso  colocado. Dessa forma, em cada repetio, a roda estar sempre com a mesma quantidade de parafusos."
                        },
                        {
                            value: "0",
                            key: "2",
                            text: "Pela Operao 7  possvel que, a cada vez,  o parafuso a ser  retirado seja diferente do parafuso retirado na repetio anterior.",
                            tip: "Sim, pela Operao 7  possvel que a cada ciclo de repetio o parafuso a ser retirado seja diferente do parafuso retirado no ciclo anterior."
                        },
                        {
                            value: "1",
                            key: "3",
                            text: "No mesmo ciclo,  possvel que o parafuso retirado seja diferente do colocado.",
                            tip: "No  possvel que isso acontea, pois sempre que a ao for executada, s haver um buraco em aberto e, portanto, o parafuso que for retirado  o que ser devolvido."
                        },
                        {
                            value: "0",
                            key: "4",
                            text: "A excluso da Operao 7.c torna o algoritmo correto para os objetivos pretendido.",
                            tip: "De fato, a repetio permitir que todos os parafusos sejam retirados e assim, aps uma quantidade de repeties idntica ao nmero de parafusos, o pneu dever ficar livre para ser retirado."
                        }
                    ]
                }
            ]
        }
    }

    render() {
        return (
            <IntegralActivity activity={this.getActivity()} />
        )
    }
}

export default AtividadeAvaliativaQuatro