import React from 'react'
import IntegralActivity from '../../../generics/activity/integral_activity'

/*ESTE COMPONENTE DEVE RECEBER COMO PROPRIEDADE O SEGUINTE ITEM:
    unitId: String, representa o id da unidade em que a atividade se encontra
.*/
class AtividadeAvaliativaTres extends React.Component {
    constructor(props) {
        super(props)

        this.validateProps()
    }

    validateProps = () => {
        if (this.props.unitId === undefined) {
            throw Error("Property 'unitId' can't be undefined!")
        }
    }

    getActivity = () => {
        return {
            unitId: this.props.unitId,
            id: "atividade_3_algoritmos",
            statement: "A03. Considere o Algoritmo 12, sobre o qual se fazem algumas afirmaes. Selecione aquela que lhe parece com menos possibilidade de estar correta.",
            questions: [
                {
                    id: "q1_a3_algoritmos",
                    statement: "",
                    title: "Algoritmo 12",
                    algorithm: true,
                    preInstructions: "<span class=\"textdarkgreen\">Considere uma pilha de pratos sujos e a inexistncia de uma pilha de pratos limpos.</span>",
                    instructions: [
                        {
                            text: "<span class=\"textred\">Enquanto</span> <span class=\"textblue\">a pilha de pratos sujos no estiver vazia</span> <span class=\"textred\">faa</span>:",
                            instructions: [
                                {
                                    text: "a. Retire o prato que est no topo da pilha de pratos sujos;"
                                },
                                {
                                    text: "b. Lave o prato sujo recm-retirado da pilha;"
                                },
                                {
                                    text: "c. Coloque o prato limpo no topo da pilha de pratos limpos."
                                }
                            ]
                        },
                    ],
                    options: [
                        {
                            value: "0",
                            key: "1",
                            text: "A execuo do algoritmo certamente produzir uma pilha vazia de pratos sujos e uma pilha de pratos limpos com a mesma quantidade de pratos que existia na pilha de pratos sujos.",
                            tip: "De fato, podemos observar que: a) o algoritmo s  interrompido quando no h mais pratos na pilha de pratos sujos e que, como cada vez que  um prato sujo  retirado, diminui de um o tamanho dessa pilha, em um determinado momento todos os pratos tero sido retirados; b) cada vez que um prato sujo  retirado, na operao seguinte ele  lavado e a seguir colocado no topo da pilha de pratos limpos; disso se pode concluir que, como a quantidade de pratos limpos cresce de forma inversa ao decrscimo da quantidade de pratos sujos, ao final o nmero de pratos limpos dever ser igual ao nmero de pratos sujos iniciais."
                        },
                        {
                            value: "0",
                            key: "2",
                            text: "O algoritmo funciona para qualquer pilha de pratos sujos, independentemente da quantidade de pratos.",
                            tip: "De fato, pois a repetio \"enquanto\" ser executada para todos os pratos ainda sujos, independente da quantidade de pratos restantes."
                        },
                        {
                            value: "0",
                            key: "3",
                            text: "O tempo necessrio para executar o Algoritmo 12, do incio ao fim,   proporcional  quantidade de pratos sujos e depende ainda do tempo mdio necessrio para lavar 1 prato.",
                            tip: "De fato, a cada ciclo da repetio teremos o tempo para tirar o prato da pilha de sujos, o tempo para lav-lo e o tempo para coloc-lo na pilha de pratos limpos. Se chamarmos a soma desses tempos de Tp e de Qp a quantidade de pratos, ento o tempo total Tt pode ser obtido pelo produto de Tp por Qp, ou seja Tt = Tp x Qp."
                        },
                        {
                            value: "1",
                            key: "4",
                            text: "Se ao executarmos a Operao 1.b, quebrarmos o prato, a execuo pode prosseguir sem qualquer problema.",
                            tip: "Se o prato for quebrado na lavagem, ento a operao 1.c no pode ser executada, pois no h operao prevista quando no h prato limpo a ser colocado na pilha de limpos."
                        }
                    ]
                }
            ]
        }
    }

    render() {
        return (
            <IntegralActivity activity={this.getActivity()} />
        )
    }
}

export default AtividadeAvaliativaTres