import React from 'react'
import IntegralActivity from '../../../generics/activity/integral_activity'

/*ESTE COMPONENTE DEVE RECEBER COMO PROPRIEDADE O SEGUINTE ITEM:
    unitId: String, representa o id da unidade em que a atividade se encontra
.*/
class AtividadeAvaliativaDois extends React.Component {
    constructor(props) {
        super(props)

        this.validateProps()
    }

    validateProps = () => {
        if (this.props.unitId === undefined) {
            throw Error("Property 'unitId' can't be undefined!")
        }
    }

    getActivity = () => {
        return {
            unitId: this.props.unitId,
            id: "atividade_2_pensamento_computacional_e_cooperacao",
            questions: [
                {
                    id: "q1_a2_pensamento_computacional_e_cooperacao",
                    title: "<span className=\"negrito\">A3</span>: Um dos elementos na realizao de um evento diz respeito  disponibilizao de equipamentos e demais recursos compartilhados entre os diferentes atores e subeventos. Um \"protocolo\" para o uso desses recursos precisa ser definido e  essencial para o sucesso do evento como um todo. A seguir apresentamos alguns exemplos de protocolos em contextos diversos. Selecione o que parece menos apropriado ao propsito.",
                    statement: "",
                    options: [
                        {
                            value: "0",
                            key: "1",
                            text: "Corredor estreito: o acesso entre dois pontos  atravs de um corredor que permite a passagem de pessoas em fila indiana, uma placa com as inscries SIGA de um lado e PARE no verso indica se o movimento em certa direo est liberado. Na existncia de uma fila, o ltimo em direo a um dos lados muda a face da placa indicando que o deslocamento na direo oposta est liberado.",
                            tip: ""
                        },
                        {
                            value: "0",
                            key: "2",
                            text: "Um jogo de palavras: iniciando no sentido horrio, cada criana em uma roda pega um carto em uma pilha de cartes e diz uma palavra que atenda a um requisito escrito no verso do carto. Quando no consegue cumprir a tarefa, o sentido muda e a criana sai da roda e ajuda uma das crianas ainda na roda a responder s prximas perguntas. Ao final vence a criana  frente do ltimo grupo na roda.",
                            tip: ""
                        },
                        {
                            value: "1",
                            key: "3",
                            text: "Faixa de pedestres: de modo a evitar o bloqueio de uma avenida movimentada, um semforo foi incorporado a uma faixa para a travessia de pedestres e o deslocamento s ocorre em um sentido por vez. O indivduo ao chegar do outro lado da avenida, aciona o semforo e libera a travessia no sentido oposto.",
                            tip: ""
                        },
                        {
                            value: "0",
                            key: "4",
                            text: "Pedidos de msicas: uma banda que anima certo evento coleta pedidos dos participantes atravs dos garons durante a confraternizao. Cada garom recolhe pedidos at que certa quantidade seja atingida ou certo tempo seja decorrido.",
                            tip: ""
                        }
                    ]
                },
                {
                    id: "q2_a2_pensamento_computacional_e_cooperacao",
                    title: "<span className=\"negrito\">A4</span>: Grande parte dos eventos acadmicos tem espao para a socializao de relatos de investigaes cientficas cobrindo uma variedade (muitas vezes elevada) de subtemas. Esses relatos so agrupados segundo diferentes critrios. Indique na lista abaixo, qual critrio seria menos indicado nesse cenrio.",
                    statement: "",
                    options: [
                        {
                            value: "1",
                            key: "1",
                            text: "Casos reais, fico, trabalho, lazer.",
                            tip: ""
                        },
                        {
                            value: "0",
                            key: "2",
                            text: "Relatos de experincia, experimentao, levantamento bibliogrfico.",
                            tip: ""
                        },
                        {
                            value: "0",
                            key: "3",
                            text: "Teoria, prtica, tcnicas e estratgias.",
                            tip: ""
                        },
                        {
                            value: "0",
                            key: "4",
                            text: "Concepo, modelagem, implementao, avaliao.",
                            tip: ""
                        },
                        {
                            value: "0",
                            key: "5",
                            text: "Completo, resumido, diagramtico.",
                            tip: ""
                        }
                    ]
                }
            ]
        }
    }

    render() {
        return (
            <IntegralActivity activity={this.getActivity()} />
        )
    }
}

export default AtividadeAvaliativaDois